#ui {
  width: calc(100% - 80px);
  max-width: calc(100% - 80px);
  margin: 40px;
  margin-top: 0px;
  /* box-sizing: border-box; */
  text-align: center;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}

.message {
  display: inline-block;
  color: white;
  font-size: 22px;
  font-weight: 200;
  text-align: center;
  margin: 70px 0px;
  max-width: 100%;
  box-sizing: border-box;
}
.message.big {
  font-size: 40px;
  margin: 120px 80px;
  padding: 50px 50px;
}
.message.big.draft {
  margin: 140px 80px;
}

@media (min-width: 600px) {
  .message.big {
    font-size: 90px;
  }
}

#hello {
  font-size: 150px;
  transform: translate(calc(50vw - 50%), calc(50vh - 70%));
  /* animation: movearound 2s infinite; */
  animation-direction: alternate;
}

/* Animation Keyframes*/
@keyframes movearound {
  from {
    transform: scale(0.3) translate(70vw, 70vh);
  }
  to {
    transform: scale(1) translate(30vw, 30vh);
  }
}
